import React from 'react';
import Container from 'react-bootstrap/Container';

export default function Header({ titolo, sottotitolo, pulsante, link }) {
return (
    <header className="d-flex flex-column justify-content-center position-relative">
        <div class="stars" aria-hidden="true"></div>
        <div class="stars2" aria-hidden="true"></div>
        <div class="stars3" aria-hidden="true"></div>
        <Container className="d-flex align-items-center">
            <div className="d-flex flex-column justify-content-center align-items-start text-lg-start mx-md-3 w-100">
                <h1 className="display-1 mb-2 mt-4 mt-lg-0 yellow accent">{titolo}</h1>
                <hr className="text-light w-100"></hr>
                <h5 className="display-6 mb-4 text-light opacity-50">{sottotitolo}</h5>
                {pulsante && link && (
                    <a
                    className="px-3 py-2 text-dark rounded-2 text-decoration-none bg-accent fw-bold"
                    href={link}
                    role="button"
                    >{pulsante}</a>
                )}
            </div>
        </Container>
    <div className="position-absolute bottom-0 w-100 h-25 headerBreak"></div>
    </header>
);
}
